import React from 'react';
// import './Rank.css';

class Signin extends React.Component {
    constructor() {
        super();
        this.state = {
            signInEmail:'',
            signInPassword: ''
        }
    }
    onEmailChange = (event) =>{
        this.setState({signInEmail:event.target.value})
    }

    onPasswordChange = (event) =>{
        this.setState({signInPassword:event.target.value})
    }

    onSubmitSignIn = () => {
        fetch('http://localhost:3000/signin', {
            method: 'post',
            header: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                email: this.state.signInEmail,
                password: this.state.signInPassword
            })
        })
        .then(response => response.json())
        .then(user => {
          if(user.id){
            this.props.loadUser(user);
            this.props.onRouteChange('home');
          }
        })
    }

    render() {
        const { onRouteChange } = this.props;
        return (
            <div>
                <article class="br3 shadow-5 pa4 black-80 mv4 w-100 w-50-m w-25-l mw6 center">
                <form action="sign-up_submit" method="get" accept-charset="utf-8">
                    <fieldset id="sign_up" class="ba b--transparent ph0 mh0">
                    <legend class="ph0 mh0 fw6 clip">Sign Up</legend>
                    <div class="mt3">
                        <label class="db fw4 lh-copy f6" htmlFor="email-address">Endereo de Email</label>
                        <input onChange={this.onEmailChange} class="pa2 input-reset ba bg-transparent w-100 measure" type="email" name="email-address"  id="email-address" />
                    </div>
                    <div class="mt3">
                        <label class="db fw4 lh-copy f6" htmlFor="password">Senha</label>
                        <input onChange={this.onPasswordChange} class="b pa2 input-reset ba bg-transparent" type="password" name="password"  id="password" />
                    </div>
                    </fieldset>
                    <div class="mt3">
                    <input 
                        onClick={this.onSubmitSignIn}
                        class="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6"
                        type="submit" value="Entrar" /></div>
                    <div class="lh-copy mt3">
                        <p onClick={()=>onRouteChange('register')} class="f6 link dim black db pointer">Registrar</p>
                    </div>
                </form>
                </article>
            </div>
        );
    }
}
    

export default Signin;


