import React from 'react';
// import './Rank.css';

class Register extends React.Component {
    constructor() {
        super();
        this.state = {
            email:'',
            password: '',
            name: ''
        }
    }

    onNameChange = (event) =>{
        this.setState({name:event.target.value})
    }

    onEmailChange = (event) =>{
        this.setState({email:event.target.value})
    }

    onPasswordChange = (event) =>{
        this.setState({password:event.target.value})
    }

    onSubmitSignIn = () => {
        fetch('http://localhost:3000/register', {
            method: 'post',
            header: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                email: this.state.email,
                password: this.state.password,
                name: this.state.name
            })
        })
            .then(response=>response.json())
            .then(user=>{
                if(user){
                    this.props.loadUser(user)
                    this.props.onRouteChange('home');
                }
            })
    }
    render() {
        const { onRouteChange } = this.props;
        return (
            <div>
                <article class="br3 shadow-5 pa4 black-80 mv4 w-100 w-50-m w-25-l mw6 center">
                <div action="sign-up_submit" method="get" accept-charset="utf-8">
                    <fieldset id="sign_up" class="ba b--transparent ph0 mh0">
                    <legend class="ph0 mh0 fw6 clip">Sign Up</legend>
                    <div class="mt3">
                        <label class="db fw4 lh-copy f6" htmlFor="name">Nome</label>
                        <input onChange={this.onNameChange} class="pa2 input-reset ba bg-transparent w-100 measure" type="text" name="name"  id="name" />
                    </div>
                    <div class="mt3">
                        <label class="db fw4 lh-copy f6" htmlFor="email-address">Endereo de Email</label>
                        <input onChange={this.onEmailChange} class="pa2 input-reset ba bg-transparent w-100 measure" type="email" name="email-address"  id="email-address" />
                    </div>
                    <div class="mt3">
                        <label class="db fw4 lh-copy f6" htmlFor="password">Senha</label>
                        <input  onChange={this.onPasswordChange} class="b pa2 input-reset ba bg-transparent" type="password" name="password"  id="password" />
                    </div>
                    </fieldset>
                    <div class="mt3">
                    <input 
                        onClick={()=>onRouteChange('home')}
                        class="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6"
                        type="submit" value="Entrar" /></div>
                </div>
                </article>
            </div>
        );
    }
}

export default Register;


